<template>
  <div class="course">
    <div class="search">
      <form action="/">
        <van-search v-model="value" show-action action-text="搜索" @cancel="onSearch" placeholder="搜索课程名称" background="#fff" @search="onSearch" @blur="blur" />
      </form>
    </div>
    <van-tabs v-if="cateList.length" v-model="active" :line-width="20" :line-height="3" :swipe-threshold="6" :title-active-color="'#000'" :border="false" @click="onClick">
      <van-tab v-for="(item,index) in cateList" :key="index" :title="item.cateName"></van-tab>
    </van-tabs>
    <div class="mid mescroll" :style="cateList.length?'height: calc(100vh - 54px - 44px - 50px);':'height: calc(100vh - 54px - 50px);'">
      <div class="list" id="list">
        <div id="listData">
          <div class="item" v-for="(item,index) in list" :key="index" @click="goDetail(item.acode)">
            <div class="left">
              <img :src="item.aimg?$utils.dealImg(item.aimg):require('@/assets/img/default/defaultH.png')" alt />
            </div>
            <div class="right">
              <p class="title">{{item.viewName}}</p>
              <div class="bottom">
                <div class="text1">{{item.aoutPrice?`${item.asaleNum}人开通`:`${item.avisitNum}人浏览`}}</div>
                <div class="text2">{{item.aoutPrice?'￥'+item.aoutPrice.toFixed(2):'免费'}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Tab, Tabs, Search } from "vant";
import { getCourseCateList, getCourseList } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Search.name]: Search
  },
  data() {
    return {
      active: 0,
      value: "",
      cateList: [],
      list: []
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    onSearch() {
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    onClick() {
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    goDetail(acode) {
      sessionStorage.setItem("acode", acode);
      this.$router.push({
        name: "courseDetail"
      });
    },
    loadCate() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getCourseCateList({ parentId: this.$route.query.cateCode || "0" }).then(
        res => {
          loading.clear();
          if (res.code == 0) {
            this.cateList = res.data;
            this.startMescroll();
          } else {
            $this.$toast.fail(res.errorMsg);
          }
        }
      );
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        pageNum: pageNum,
        pageSize: pageSize,
        cateCode: this.cateList.length
          ? this.cateList[this.active].cateCode
          : this.$route.query.cateCode,
        viewName: this.value
      };
      getCourseList(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.errorMsg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据"
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true // 是否开启懒加载,默认false
          }
        }
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          if (page.num == 1) $this.list = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this.list.push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = "课程中心";
    if (this.$route.query.value) {
      this.value = this.$route.query.value;
    }
    this.loadCate();
  }
};
</script>
<style lang="scss" scoped>
.course {
  .mid {
    background: #fff;
    .list {
      padding: 10px;
      .item {
        border-bottom: 1pt solid #efefef;
        margin-bottom: 10px;
        padding: 5px 0;
        display: flex;
        background: #fff;
        .left {
          img {
            width: 120px;
            margin-right: 5px;
          }
        }
        .right {
          position: relative;
          flex: 2;
          .title {
            font-size: 13px;
            line-height: 20px;
            max-height: 50px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            overflow: hidden;
          }
          .bottom {
            position: absolute;
            bottom: 0;
            left: 0;
            display: flex;
            width: 100%;
            line-height: 30px;
            .text1 {
              color: #999;
              font-size: 13px;
              flex: 2;
            }
            .text2 {
              flex: 1;
              text-align: right;
              color: #9e1435;
              font-size: 13px;
            }
          }
        }
      }
    }
  }
}
</style>
<style lang="scss">
.course {
  .van-tabs__wrap {
    border-bottom: 1pt solid #e0e0e0;
  }
}
</style>
